	export default {
        onLoad(options) {
            this.name = options.name
            this.total = options.total?Number(options.total):0
        },
		data() {
			return {
                name:'',
				total:0,
			}
		},
		methods: {
			goChapter(item){
                uni.showToast({
                    title:'进入' + item + '章!'
                })
            }
		}
	}
